body {
    font-family: "Montserrat", sans-serif;
    background: white;
  }
  
.container {
    display: block;
    max-width: 700px;
    width: 80%;
    margin: 120px auto;
}
  
  h1 {
    color: #13264e;
    font-size: 48px;
    letter-spacing: -3px;
    text-align: center;
    margin: 120px 0 80px 0;
    transition: 0.2s linear;
  }
  
  .links {
    list-style-type: none;
  }
  .links li {
    display: inline-block;
    margin: 0 20px 0 0;
    transition: 0.2s linear;
    text-decoration: none;
    color: #0f132a;
    font-weight: bolder;
    text-align: center;
    cursor: pointer;
  }

  form {
    width: 100%;
    max-width: 680px;
    margin: 40px auto 10px;
  }
  form .input__block {
    margin: 20px auto;
    display: block;
    position: relative;
  }
  form .input__block.first-input__block::before {
    content: "";
    position: absolute;
    top: -15px;
    left: 50px;
    display: block;
    width: 0;
    height: 0;
    background: transparent;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid rgba(15, 19, 42, 0.1);
    transition: 0.2s linear;
  }
  form .input__block.signup-input__block::before {
    content: "";
    position: absolute;
    top: -15px;
    left: 130px;
    display: block;
    width: 0;
    height: 0;
    background: transparent;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid rgba(15, 19, 42, 0.1);
    transition: 0.2s linear;
  }
  form .input__block input {
    display: block;
    width: 90%;
    max-width: 680px;
    height: 50px;
    margin: 0 auto;
    border-radius: 8px;
    border: none;
    background: rgba(15, 19, 42, 0.1);
    color: rgba(15, 19, 42, 0.3);
    padding: 0 0 0 15px;
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
  }
  form .input__block input:focus, form .input__block input:active {
    outline: none;
    border: none;
    color: #0f132a;
  }
  form .input__block input.repeat__password {
    opacity: 0;
    display: none;
    transition: 0.2s linear;
  }
  form .signin__btn {
    background: #13264e;
    color: white;
    display: block;
    width: 92.5%;
    max-width: 680px;
    height: 50px;
    border-radius: 8px;
    margin: 0 auto;
    border: none;
    cursor: pointer;
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
    box-shadow: 0 0 0 rgba(19, 32, 78, 0);
    transition: 0.2s linear;
  }
  form .signin__btn:hover {
    box-shadow: 0 15px 30px rgba(19, 32, 78, 0.36);
  }
  
  .separator {
    display: block;
    margin: 30px auto 10px;
    text-align: center;
    height: 40px;
    position: relative;
    background: transparent;
    color: rgba(15, 19, 42, 0.4);
    font-size: 13px;
    width: 90%;
    max-width: 680px;
  }
  .separator::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    background: rgba(15, 19, 42, 0.2);
    height: 1px;
    width: 45%;
  }
  .separator::after {
    content: "";
    position: absolute;
    top: 8px;
    right: 0;
    background: rgba(15, 19, 42, 0.2);
    height: 1px;
    width: 45%;
  }
  
  .google__btn,
  .naver__btn {
    display: block;
    width: 90%;
    max-width: 680px;
    margin: 20px auto;
    height: 50px;
    cursor: pointer;
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
    border-radius: 8px;
    border: none;
    line-height: 40px;
  }
  .google__btn.google__btn,
  .naver__btn.google__btn {
    background: #5b90f0;
    color: white;
    box-shadow: 0 0 0 rgba(91, 144, 240, 0);
    transition: 0.2s linear;
  }
  .google__btn.google__btn .fa,
  .naver__btn.google__btn .fa {
    font-size: 20px;
    padding: 0 5px 0 0;
  }
  .google__btn.google__btn:hover,
  .naver__btn.google__btn:hover {
    box-shadow: 0 15px 30px rgba(91, 144, 240, 0.36);
  }
  .google__btn.naver__btn,
  .naver__btn.naver__btn {
    background: #03c75a;
    color: white;
    box-shadow: 0 0 0 rgba(3, 199, 90, 0);
    transition: 0.2s linear;
  }
  .google__btn.naver__btn .fa,
  .naver__btn.naver__btn .fa {
    font-size: 20px;
    padding: 0 5px 0 0;
  }
  .google__btn.naver__btn:hover,
  .naver__btn.naver__btn:hover {
    box-shadow: 0 15px 30px rgba(3, 199, 90, 0.36);
  }